.event-hover-slide li {
  position: relative;
  overflow: hidden;
}

.event-hover-slide p {
  display: inline-block;
  position: relative;
  z-index: 1;
  transition: 0.35s ease color;
}

.event-hover-slide p:before {
  content: "";
  display: inline-block;
  z-index: -1;
  position: absolute;
  box-sizing: border-box;
  left: calc(-100% + 5px);
  top: 0;
  width: 100%;
  height: 100%;
  border-right: solid 5px #ff2424;
  background: #c4a546;
  transition: 0.35s ease left;
}

.event-hover-slide p.is-current:before,
.event-hover-slide p:hover:before {
  left: 0;
}

.event-main-nav p {
  display: flex;
  justify-content: left;
  padding-left: 5vww;
